        return {
            showSection: 'basicInfo', // ÂΩìÂâçÊòæÁ§∫ÁöÑÈÉ®ÂàÜ
            username: '', // Áî®Êà∑Âêç
            gender: '0', // ÊÄßÂà´
            faction: '', // Á≠æÂêç
            phone: '', // ÊâãÊú∫Âè∑
            address: '', // Âú∞ÂùÄ
            oldPassword: '', // ÂéüÂØÜÁ†Å
            newPassword: '', // Êñ∞ÂØÜÁ†Å
            IsnewPassword: '', // Á°ÆËÆ§ÂØÜÁ†Å
            Visiable: false, // ÂØπËØùÊ°ÜÂèØËßÅÊÄß
            hobbies: '', // ÂÖ¥Ë∂£Áà±Â•Ω
            age: '',
            height: '',
            weight: '',
            bloodType: '',
            emergencyContact: '',
            emergencyPhone: '',
            allergies: '',
            chronicDiseases: '',
