    // ÁªÑ‰ª∂ÊñπÊ≥ï
    methods: {
        // ÂàùÂßãÂåñÁî®Êà∑‰ø°ÊÅØ
        async initUserInfo() {
            this.username = (localStorage.getItem('username') || 'ÈªòËÆ§Áî®Êà∑Âêç').replace(/['"]/g, '');
            this.gender = (localStorage.getItem('gender') || '0').replace(/['"]/g, '');
            this.faction = (localStorage.getItem('faction') || 'ÈªòËÆ§Á≠æÂêç').replace(/['"]/g, '');
            this.phone = (localStorage.getItem('phone') || 'ÈªòËÆ§ÊâãÊú∫Âè∑').replace(/['"]/g, '');
            this.address = (localStorage.getItem('address') || 'ÈªòËÆ§Âú∞ÂùÄ').replace(/['"]/g, '');
            this.hobbies = (localStorage.getItem('hobbies') || 'ÊöÇÊó†ÂÖ¥Ë∂£Áà±Â•Ω').replace(/['"]/g, '');
            this.age = (localStorage.getItem('age') || 'Êú™ËÆæÁΩÆ').replace(/['"]/g, '');
            this.height = (localStorage.getItem('height') || 'Êú™ËÆæÁΩÆ').replace(/['"]/g, '');
            this.weight = (localStorage.getItem('weight') || 'Êú™ËÆæÁΩÆ').replace(/['"]/g, '');
            this.bloodType = (localStorage.getItem('bloodType') || 'Êú™ËÆæÁΩÆ').replace(/['"]/g, '');
            this.emergencyContact = (localStorage.getItem('emergencyContact') || 'Êú™ËÆæÁΩÆ').replace(/['"]/g, '');
            this.emergencyPhone = (localStorage.getItem('emergencyPhone') || 'Êú™ËÆæÁΩÆ').replace(/['"]/g, '');
            this.allergies = (localStorage.getItem('allergies') || 'Êó†').replace(/['"]/g, '');
            this.chronicDiseases = (localStorage.getItem('chronicDiseases') || 'Êó†').replace(/['"]/g, '');
        },

        // Â§ÑÁêÜÁºñËæëÊåâÈíÆÁÇπÂáª‰∫ã‰ª∂
        handleClick(data) {
            this.Visiable = true;
            this.$nextTick(() => {
                this.$refs.dialog.init(data);
