        },

        // ‰øÆÊîπÂØÜÁ†ÅÊñπÊ≥ï
        async changePassword() {
            // Ë°®ÂçïÈ™åËØÅ
            if (!this.validatePasswordForm()) {
                return;
            }

            try {
                const id = localStorage.getItem('id');
                const response = await this.$axios.post(`/api/oldman/changePassword/${id}`, {
                    password: this.IsnewPassword
                });

                if (response.data.code === 200) {
                    this.$message.success('ÂØÜÁ†Å‰øÆÊîπÊàêÂäü');
                    this.resetPasswordForm();
                } else {
                    this.$message.error(response.data.message || 'ÂØÜÁ†Å‰øÆÊîπÂ§±Ë¥•');
                }
            } catch (error) {
                console.error('‰øÆÊîπÂØÜÁ†ÅÂ§±Ë¥•:', error);
                this.$message.error('ÂØÜÁ†Å‰øÆÊîπÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï');
            }
        },

        // È™åËØÅÂØÜÁ†ÅË°®Âçï
        validatePasswordForm() {
            if (!this.oldPassword || !this.newPassword || !this.IsnewPassword) {
                this.$message.warning('ËØ∑Â°´ÂÜôÂÆåÊï¥ÁöÑÂØÜÁ†Å‰ø°ÊÅØ');
                return false;
            }

            if (this.newPassword !== this.IsnewPassword) {
                this.$message.warning('‰∏§Ê¨°ËæìÂÖ•ÁöÑÊñ∞ÂØÜÁ†Å‰∏ç‰∏ÄËá¥');
                return false;
            }

            if (this.newPassword.length < 6) {
                this.$message.warning('Êñ∞ÂØÜÁ†ÅÈïøÂ∫¶‰∏çËÉΩÂ∞ë‰∫é6‰Ωç');
                return false;
            }

            return true;
        },

        // ÈáçÁΩÆÂØÜÁ†ÅË°®Âçï
        resetPasswordForm() {
            this.oldPassword = '';
            this.newPassword = '';
            this.IsnewPassword = '';
        },

        // Êõ¥Êñ∞Áî®Êà∑Âü∫Êú¨‰ø°ÊÅØ
        async updateUserInfo(data) {
            try {
                const id = localStorage.getItem('id');
                const response = await this.$axios.post(`/api/oldman/updateInfo/${id}`, data);

                if (response.data.code === 200) {
                    this.$message.success('‰ø°ÊÅØÊõ¥Êñ∞ÊàêÂäü');
                    this.initUserInfo(); // ÈáçÊñ∞Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
                } else {
                    this.$message.error(response.data.message || '‰ø°ÊÅØÊõ¥Êñ∞Â§±Ë¥•');
                }
            } catch (error) {
                console.error('Êõ¥Êñ∞Áî®Êà∑‰ø°ÊÅØÂ§±Ë¥•:', error);
                this.$message.error('‰ø°ÊÅØÊõ¥Êñ∞Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï');
            }
        }
    },
