<template #bodyCell="{row}">
  <!-- ‰øÆÊîπ dialog Ê†∑ÂºèÔºåÊ∑ªÂä†ÂúÜËßí -->
  <el-dialog title="Êõ¥Êñ∞" :visible.sync="detailVisible" width="60%" custom-class="custom-dialog">
    <el-form :label-position="labelPosition" label-width="100px" :model="formLabelAlign" class="form-container">
      <!-- Â∑¶‰æßË°®Âçï -->
      <div class="form-column">
        <el-form-item label="ÊòµÁß∞">
          <el-input v-model="formLabelAlign.username" placeholder="ËØ∑ËæìÂÖ•ÊòµÁß∞"></el-input>
        </el-form-item>
        <el-form-item label="ÊâãÊú∫Âè∑">
          <el-input v-model="formLabelAlign.phone" placeholder="ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑"></el-input>
        </el-form-item>
        <el-form-item label="Âπ¥ÈæÑ">
          <el-input v-model="formLabelAlign.age" placeholder="ËØ∑ËæìÂÖ•Âπ¥ÈæÑ"></el-input>
        </el-form-item>
        <el-form-item label="Ë∫´È´ò">
          <el-input v-model="formLabelAlign.height" placeholder="ËØ∑ËæìÂÖ•Ë∫´È´ò(cm)"></el-input>
        </el-form-item>
        <el-form-item label="‰ΩìÈáç">
          <el-input v-model="formLabelAlign.weight" placeholder="ËØ∑ËæìÂÖ•‰ΩìÈáç(kg)"></el-input>
        </el-form-item>
        <el-form-item label="Ë°ÄÂûã">
          <el-select v-model="formLabelAlign.bloodType" placeholder="ËØ∑ÈÄâÊã©Ë°ÄÂûã">
            <el-option label="AÂûã" value="A"></el-option>
            <el-option label="BÂûã" value="B"></el-option>
            <el-option label="OÂûã" value="O"></el-option>
            <el-option label="ABÂûã" value="AB"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="ÂÖ¥Ë∂£Áà±Â•Ω">
          <el-select v-model="formLabelAlign.hobbies" multiple placeholder="ËØ∑ÈÄâÊã©ÊÇ®ÁöÑÂÖ¥Ë∂£Áà±Â•Ω">
            <el-option v-for="item in hobbyOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </div>

      <!-- Âè≥‰æßË°®Âçï -->
      <div class="form-column">
        <el-form-item label="Á¥ßÊÄ•ËÅîÁ≥ª‰∫∫">
          <el-input v-model="formLabelAlign.emergencyContact" placeholder="ËØ∑ËæìÂÖ•Á¥ßÊÄ•ËÅîÁ≥ª‰∫∫ÂßìÂêç" style="min-width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="Á¥ßÊÄ•ÁîµËØù">
          <el-input v-model="formLabelAlign.emergencyPhone" placeholder="ËØ∑ËæìÂÖ•Á¥ßÊÄ•ËÅîÁ≥ªÁîµËØù"></el-input>
        </el-form-item>
        <el-form-item label="ËøáÊïèÂè≤">
          <el-input type="textarea" v-model="formLabelAlign.allergies" placeholder="ËØ∑ËæìÂÖ•ËøáÊïèÂè≤"></el-input>
        </el-form-item>
        <el-form-item label="ÊÖ¢ÊÄßÁóÖÂè≤">
          <el-input type="textarea" v-model="formLabelAlign.chronicDiseases" placeholder="ËØ∑ËæìÂÖ•ÊÖ¢ÊÄßÁóÖÂè≤"></el-input>
        </el-form-item>
        <el-form-item label="Á≠æÂêç">
          <el-input v-model="formLabelAlign.faction" placeholder="ËØ∑ËæìÂÖ•Á≠æÂêçÔºå‰∏çËÉΩË∂ÖËøá126‰∏™Â≠ó"></el-input>
        </el-form-item>
        <el-form-item label="Âú∞Âå∫">
          <el-select v-model="formLabelAlign.address" placeholder="ËØ∑ÈÄâÊã©ÊÇ®ÁöÑÂú∞Âå∫">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

      </div>
    </el-form>

    <!-- Ë∞ÉÊï¥ÊåâÈíÆ‰ΩçÁΩÆ -->
    <div class="dialog-footer">
      <el-button @click="save" type="primary" round>‰øùÂ≠ò</el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: "dialogComponent",
  data() {
    return {
      detailVisible: false,
      labelPosition: 'right',
      formLabelAlign: {
        username: '',
        phone: '',
        faction: '',
      },
      // Êñ∞Â¢ûÂÖ¥Ë∂£Áà±Â•ΩÈÄâÈ°πÊï∞ÊçÆ
      hobbyOptions: [{
        value: 'ÈòÖËØª',
        label: 'ÈòÖËØª'
      }, {
        value: '‰∏ãÊ£ã',
        label: '‰∏ãÊ£ã'
      }, {
        value: '‰π¶Ê≥ï',
        label: '‰π¶Ê≥ï'
      }, {
        value: 'ÁªòÁîª',
        label: 'ÁªòÁîª'
      }, {
        value: 'Âõ≠Ëâ∫',
        label: 'Âõ≠Ëâ∫'
      }, {
        value: 'Â§™ÊûÅ',
        label: 'Â§™ÊûÅ'
      }],
      //Âú∞Âå∫ÂÜÖÂÆπ
      options: [{
        value: 'Âåó‰∫¨',
        label: 'Âåó‰∫¨'
      }, {
        value: '‰∏äÊµ∑',
        label: '‰∏äÊµ∑'
      }, {
        value: 'Âπø‰∏ú',
        label: 'Âπø‰∏ú'
      }, {
        value: 'ÂÆâÂæΩ',
        label: 'ÂÆâÂæΩ'
      }, {
        value: 'Ê≤≥Âçó',
        label: 'Ê≤≥Âçó'
      }, {
        value: 'ÂÖ∂‰ªñ',
        label: 'ÂÖ∂‰ªñ'
      }],
      value: ''
    }
  },

  methods: {
    init(id) {
      this.detailVisible = true;
      this.fetchData(id);
    },
    // ÂõûÊòæ
    fetchData() {
      const id = localStorage.getItem('id');
      this.$axios
        .get(`/api/oldman/get/${id}`)
        .then(response => {
          if (response.data.code === 200) {
            const data = response.data.data;
            this.formLabelAlign = {
              username: data.username,
              phone: data.phone,
              faction: data.faction,
              address: data.address,
              hobbies: data.hobbies, // ÂÖ¥Ë∂£Áà±Â•Ω
              age: data.age,
              height: data.height,
              weight: data.weight,
              bloodType: data.bloodType,
              emergencyContact: data.emergencyContact,
              emergencyPhone: data.emergencyPhone,
              allergies: data.allergies,
              chronicDiseases: data.chronicDiseases,
            };
            console.log(data);
          } else {
            this.$message.error('Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•');
          }
        })
        .catch(error => {
          if (error.response) {
            console.error('ÊúçÂä°Âô®ÂìçÂ∫îÈîôËØØ:', error.response.data);
            console.error('Áä∂ÊÄÅÁ†Å:', error.response.status);
            console.error('ÂìçÂ∫îÂ§¥:', error.response.headers);
            this.$message.error('Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•: ' + error.response.data.message);
          } else if (error.request) {
            console.error(error.request);
          } else {
            console.error('Error', error.message);
          }
          this.$message.error('Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•');
        });
    },
    // Êõ¥Êñ∞Êï∞ÊçÆ
    save() {
      const id = localStorage.getItem('id');
      // Á°Æ‰øù hobbies ÊòØÊï∞ÁªÑÊ†ºÂºèÔºåÂπ∂‰∏îËΩ¨Êç¢‰∏∫Â≠óÁ¨¶‰∏≤
      const hobbiesData = Array.isArray(this.formLabelAlign.hobbies)
        ? this.formLabelAlign.hobbies.join(',')  // Â∞ÜÊï∞ÁªÑËΩ¨Êç¢‰∏∫ÈÄóÂè∑ÂàÜÈöîÁöÑÂ≠óÁ¨¶‰∏≤
        : '';
      this.$axios
        .put(`/api/oldman/save/${id}`, {
          username: this.formLabelAlign.username,
          phone: this.formLabelAlign.phone,
          faction: this.formLabelAlign.faction,
          address: this.formLabelAlign.address,
          hobbies: hobbiesData, // Á°Æ‰øùÂèëÈÄÅÊï∞ÁªÑÊ†ºÂºè
          age: this.formLabelAlign.age,
          height: this.formLabelAlign.height,
          weight: this.formLabelAlign.weight,
          bloodType: this.formLabelAlign.bloodType,
          emergencyContact: this.formLabelAlign.emergencyContact,
          emergencyPhone: this.formLabelAlign.emergencyPhone,
          allergies: this.formLabelAlign.allergies,
          chronicDiseases: this.formLabelAlign.chronicDiseases,
        })
        .then(response => {
          if (response.data.code === 200) {
            localStorage.setItem('username', this.formLabelAlign.username);
            localStorage.setItem('phone', this.formLabelAlign.phone);
            localStorage.setItem('faction', this.formLabelAlign.faction);
            localStorage.setItem('address', this.formLabelAlign.address);
            localStorage.setItem('hobbies', JSON.stringify(hobbiesData)); // Â∞ÜÊï∞ÁªÑËΩ¨Êç¢‰∏∫Â≠óÁ¨¶‰∏≤Â≠òÂÇ®
            localStorage.setItem('age', this.formLabelAlign.age);
            localStorage.setItem('height', this.formLabelAlign.height);
            localStorage.setItem('weight', this.formLabelAlign.weight);
            localStorage.setItem('bloodType', this.formLabelAlign.bloodType);
            localStorage.setItem('emergencyContact', this.formLabelAlign.emergencyContact);
            localStorage.setItem('emergencyPhone', this.formLabelAlign.emergencyPhone);
            localStorage.setItem('allergies', this.formLabelAlign.allergies);
            localStorage.setItem('chronicDiseases', this.formLabelAlign.chronicDiseases);
            
            this.$message.success('Êõ¥Êñ∞ÊàêÂäü');
            // ÂÖ≥Èó≠ÂØπËØùÊ°Ü
            this.detailVisible = false;
            // Âà∑Êñ∞È°µÈù¢
            window.location.reload();
          }
          else {
            this.$message.error('Êõ¥Êñ∞Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ËæìÂÖ•ÊòØÂê¶Ê≠£Á°Æ');
          }
        })
        .catch(error => {
          console.error('Êõ¥Êñ∞Â§±Ë¥•:', error);
          this.$message.error('Êõ¥Êñ∞Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ËæìÂÖ•ÊòØÂê¶Ê≠£Á°Æ');
        });
    }
  }
}
</script>

